.lightbox-overlay-dark {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 9999;
}

.modal-content-light {
  position: absolute;
  top: 120px;
  left: 0;
  right: 0;
  bottom: 0;
  border: none;
  background: #fff;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  border-radius: 10px;
  outline: none;
  padding: 0;
}

.modal-content-light .btn-lightbox-close {
  position: fixed;
  right: 0;
  margin: 10px;
  cursor: pointer;
  content: url("data:image/svg+xml,%3Csvg width='14' height='14' viewBox='0 0 14 14' fill='none' xmlns='http://www.w3.org/2000/svg' %3E%3Cpath fillRule='evenodd' clipRule='evenodd' d='M2.35813 1.63696L7.00002 6.27018L11.6419 1.63696L12.3631 2.35813L7.72986 7.00002L12.3631 11.6419L11.6419 12.3631L7.00002 7.72986L2.35813 12.3631L1.63696 11.6419L6.27018 7.00002L1.63696 2.35813L2.35813 1.63696Z' fill='%23444' %3E%3C/path%3E%3C/svg%3E");
}

.modal-content-dark {
  position: absolute;
  display: grid;
  place-items: center;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border: none;
  background: transparent;
  outline: none;
  padding: 5%;
}

.modal-content-dark .btn-lightbox-close {
  position: absolute;
  top: 0;
  right: 0;
  margin: 10px;
  cursor: pointer;
  content: url("data:image/svg+xml,%3Csvg width='14' height='14' viewBox='0 0 14 14' fill='none' xmlns='http://www.w3.org/2000/svg' %3E%3Cpath fillRule='evenodd' clipRule='evenodd' d='M2.35813 1.63696L7.00002 6.27018L11.6419 1.63696L12.3631 2.35813L7.72986 7.00002L12.3631 11.6419L11.6419 12.3631L7.00002 7.72986L2.35813 12.3631L1.63696 11.6419L6.27018 7.00002L1.63696 2.35813L2.35813 1.63696Z' fill='%23fff' %3E%3C/path%3E%3C/svg%3E");
}

.modal-content-dark img {
  width: auto;
  max-height: 80vh;
}
